/* components/home-components/FacilitiesSlider/facilities.module.css */

.fourthSliderSection {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.headingContainer {
  position: relative;
  z-index: 10;
  padding: 2rem;
  background: transparent;
}

.headingContent {
  align-items: center;
}

.headingIcon {
  display: flex;
  align-items: center;
}

.blueText {
  color: #1e40af;
}

.sliderStickyWrapper {
  position: relative;
  width: 100%;
  height: 100vh;
}

.fixedSlider {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 5;
}

.swiperContainer {
  width: 100%;
  height: 100vh;
  position: relative;
}

.parallaxBg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 120%;
  background: linear-gradient(135deg, rgba(176, 143, 41, 0.1) 0%, rgba(0, 0, 0, 0.1) 100%);
  z-index: -1;
}

.parallaxWrapper {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.slideContainer {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.slideImage {
  object-fit: cover;
  transition: transform 0.3s ease-out;
}

.verticalLine {
  position: absolute;
  left: 50px;
  top: 0;
  bottom: 0;
  width: 1px;
  background: rgba(255, 255, 255, 0.3);
  z-index: 2;
}

.slideNumberBox {
  position: absolute;
  top: 50%;
  left: -20px;
  width: 40px;
  height: 40px;
  background: #b08f29;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 14px;
  transform: translateY(-50%);
}

.bannerContent {
  position: absolute;
  bottom: 150px;
  left: 100px;
  color: white;
  z-index: 3;
  max-width: 500px;
}

.bannerContent h2 {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 1rem;
  text-transform: uppercase;
}

.bannerContent p {
  font-size: 1.1rem;
  line-height: 1.6;
  opacity: 0.9;
}

.bannerLinks {
  position: absolute;
  bottom: 80px;
  left: 100px;
  z-index: 3;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.bannerLink {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: white;
  text-decoration: none;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: all 0.3s ease;
}

.bannerLink:hover {
  color: #b08f29;
  transform: translateX(10px);
}

.slideProgress {
  position: absolute;
  bottom: 50px;
  right: 50px;
  color: white;
  z-index: 3;
  font-size: 1rem;
  letter-spacing: 1px;
}

/* Smooth transitions for all parallax elements */
[data-swiper-parallax] {
  transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

/* Additional smooth scrolling enhancements */
.sliderStickyWrapper {
  transition: all 0.3s ease;
}

.bannerContent h2 {
  transition: transform 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.bannerContent p {
  transition: transform 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.bannerLink {
  transition: all 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transform: translateX(0);
}

.slideNumberBox {
  transition: all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.slideProgress span {
  transition: all 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

/* Responsive Design */
@media (max-width: 768px) {
  .headingContainer {
    padding: 1rem;
  }
  
  .bannerContent {
    left: 50px;
    bottom: 200px;
    max-width: 300px;
  }
  
  .bannerContent h2 {
    font-size: 2rem;
  }
  
  .bannerContent p {
    font-size: 1rem;
  }
  
  .bannerLinks {
    left: 50px;
    bottom: 100px;
  }
  
  .verticalLine {
    left: 25px;
  }
  
  .slideNumberBox {
    left: -15px;
    width: 30px;
    height: 30px;
    font-size: 12px;
  }
}

@media (max-width: 480px) {
  .bannerContent {
    left: 25px;
    bottom: 220px;
    max-width: 250px;
  }
  
  .bannerContent h2 {
    font-size: 1.5rem;
  }
  
  .bannerContent p {
    font-size: 0.9rem;
  }
  
  .bannerLinks {
    left: 25px;
    bottom: 120px;
  }
  
  .bannerLink {
    font-size: 0.9rem;
  }
}